body {
	font: 14px "Century Gothic", Futura, sans-serif;
	margin: 20px;
	background: #f4f4f4;
}

body p {
	color:#888;
}

.add-oscillator button {
	width:100px;
	color:#fff;
	height:30px;
	border:1px solid #e7e7e7;
	background: #888;
	outline:0;
	border-radius: 6px;
	cursor: pointer;
}

.oscillator-row {
	border:2px solid #fff;
	margin: 1%;
	padding: 12px;
	background: #fff;
	border-radius: 6px;
	display:inline-block;
	min-width:420px;
	background:#f9f9f9;
}

.control-row {
	color:#999;
	margin:10px;
}

.control-row:after {
	clear: both;
	content: "";
	display: table;
}

.control-row.waveform {
	margin:0 0 0 10px;
}

.control-row p {
	color:#999;
}

.control-row button {
	margin: 4px 0px 4px 6px;
	padding:6px 14px;
	font-size: 14px;
	cursor: pointer;
	font-weight: 700;
	border-radius: 5px;
	color:#808080;
	background: #f2f2f2;
}

.control-row button:hover,
.control-row button:focus {
	background: #fff;
	-webkit-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
	outline:0;
}

.control-row input {
	width:70%;
}

.control-row input:focus {
	width:70%;
	outline:0;
}

.control-row p {
	line-height: 0pt;
	margin:10px;
	float:left;
	width:120px;
}
* { -webkit-box-sizing:border-box; box-sizing:border-box; }

ul.keys {
	height:18.875em;
	width:auto;
  	margin:0;
  	padding:2em;
  	position:relative;
}

li { margin:0; padding:0; list-style:none; position:relative; float:left; }

.keys .white {
	height:16em; width:4em; z-index:1;
	border-left:1px solid #e8e8e8;
	border-bottom:1px solid #e8e8e8;
	border-radius:0 0 5px 5px;
	-webkit-box-shadow:-1px 0 0 rgba(255,255,255,0.1) inset, 0 0 1px #ccc inset, 0 0 1px rgba(0,0,0,0.1);
	        box-shadow:-1px 0 0 rgba(255,255,255,0.1) inset, 0 0 1px #ccc inset, 0 0 1px rgba(0,0,0,0.1);
	background: #fff;
}

.keys .white:active {
	border-top:1px solid #e2e2e0;
	border-left:1px solid #e2e2e0;
	border-bottom:1px solid #e2e2e0;
	-webkit-box-shadow:1px 0 1px rgba(0,0,0,0.1) inset, -2px 2px 10px rgba(0,0,0,0.1) inset,0 0 1px rgba(0,0,0,0.1);
	        box-shadow:1px 0 1px rgba(0,0,0,0.1) inset, -2px 2px 10px rgba(0,0,0,0.1) inset,0 0 1px rgba(0,0,0,0.1);
	background: #e9e9e9
}

.keys .black {
	height:8em;
	width:2em;
	margin:0 0 0 -1em;
	z-index:2;
	border:0px solid #999;
	border-radius:0 0 6px 6px;
	background: #666;
}

.keys .black:active {
	background:#222
}

.w2,.w3,.w4,.w6,.w7,.w9,.w10,.w11,.w13,.w14 { margin:0 0 0 -1em;  }

.keys li:first-child { border-radius:5px 0 5px 5px; }
.keys li:last-child { border-radius:0 5px 5px 5px; }



